import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, InfoRow, List, Cell, Footer } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import './Main.css';

const osname = platform();

const Prizes = props => (
  <Panel id={props.id}>
    <PanelHeader
      noShadow
      addon={<HeaderButton onClick={props.go} data-to="home">Назад</HeaderButton>}
      left={<HeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
    >
      Призы
    </PanelHeader>
    <Group>
      <List>
        <Cell>
          <InfoRow title="Первое место">
                  Смартфон iPhone XS Max, 512 ГБ, цвет «Серый космос»
                </InfoRow>
              </Cell>
              <Cell>
                <InfoRow title="Второе место">
                  Экшн-камера GoPro Hero 7 Black + стабилизатор-монопод для смартфона DJI Osmo Mobile 2
                </InfoRow>
              </Cell>
              <Cell>
                <InfoRow title="Третье место">
                  Экшн-камера GoPro Hero 7 Black
                </InfoRow>
              </Cell>
            </List>
          </Group>
          <Footer>3 приза</Footer>
  </Panel>
);

Prizes.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired
};

export default Prizes;



