import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, List, Cell} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { Link } from '@vkontakte/vkui';
import './Main.css';

const osname = platform();

const Rules = props => (
  <Panel id={props.id}>
    <PanelHeader
      noShadow
      addon={<HeaderButton onClick={props.go} data-to="home">Назад</HeaderButton>}
      left={<HeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
    >
      Правила
    </PanelHeader>
    <Group>
        <List>
          <Cell>
            Бинго представляет собой таблицу из 20 клеток. Чтобы завершить бинго, нужно выполнить задания из любой строки и любого столбца. То есть всего нужно сделать <nobr>7–8</nobr> заданий. Выбирать задания по диагонали нельзя.
          </Cell>
          <Cell>
            Каждое задание нужно выполнить в прямом эфире с мобильного приложения ВКонтакте для Android или iOS.
          </Cell>
          <Cell>
            Перед началом трансляции зайдите в настройки трансляций и убедитесь, что они не скрыты приватностью и будут опубликованы на стене страницы или сообщества. Иначе жюри не смогут оценить Ваши работы.
          </Cell>
          <Cell>
            Укажите в названии вашей трансляции тематический хештег: <strong>#BingoNewYearStreamers</strong>.
          </Cell>
          <Cell>
            Чтобы жюри было удобнее оценивать работы, помимо хэштега укажите в названии трансляции тему выполняемого задания.
          </Cell>
          <Cell>
            Одно задание = одна работа. Нельзя снять универсальную трансляцию, которая закроет сразу несколько клеток.
          </Cell>
          <Cell>
            В бинго участвуют только новые работы. Записи старых трансляций не принимаются.
          </Cell>
          <Cell>
            Выполнять задания можно в любом удобном порядке.
          </Cell>
          <Cell>
            Срок выполнения — 20 дней, работы принимаются до 13 января включительно.
          </Cell>
          <Cell>
            После выполнения всех заданий, пожалуйста, заполните <Link href="https://vk.com/authors">форму участника</Link>.         
          </Cell>
        </List>
      </Group>
  </Panel>
);

Rules.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired
};

export default Rules;



